import React, { Component } from "react";
//import { Link } from "react-router-dom";
import WarningComponent from "../../commoncomponent/warning.js";
import ErrorAlert from "../../commoncomponent/ErrorAlert";
import axios from "axios";
import { API_ROOT, TOKEN, INTERVAL, HOSTNETWORK, HOSTNETWORKNAME } from '../../config';
import { callErrorToast, callSuccessToast } from '../../helper';
import ButterToast, { POS_UP, POS_BOTTOM, POS_RIGHT } from 'butter-toast';
import Modal from 'react-modal';


const customStyles = {
    content: {
        top: "50%",
        left: "50%",
        right: "auto",
        bottom: "auto",
        marginRight: "-50%",
        transform: "translate(-50%, -50%)"
    }
};


export default class Transfer extends Component {

    constructor() {
        super();
        this.state = {
            fromAddress: '',
            walletBalance: null,
            dailyData: [],
            tokenBalance: null,
            dataLoading: true,
            toAddress: '',
            sendingAmount: '',
            contractAddress: '',
            modalIsOpen: false,
            networkErrorAlert: false,
            hostError: false,
        }
    }

    componentDidMount() {
        console.log("env var", process.env.NODE_ENV);

        window.setInterval(() => {

            if (window.tronWeb && window.tronWeb.defaultAddress && window.tronWeb.defaultAddress.base58) {

                this.setState({
                    fromAddress: window.tronWeb.defaultAddress.base58
                })

                if (window.tronWeb && window.tronWeb.defaultAddress && window.tronWeb.solidityNode.host == HOSTNETWORK) {
                    this.setState({
                        hostError: false,
                    })

                    // console.log(window.tronWeb);

                    /** start get the trx balance */
                    let balance = window.tronWeb.trx.getBalance(this.state.fromAddress);
                    balance.then((result) => {
                        this.setState({ walletBalance: result })
                    })
                    /** end */


                    /** start get daily data based on the address */
                    axios.get(`${API_ROOT}` + '/api/tron/getTokenTransactionsByAddress?address=' + this.state.fromAddress, {

                        headers: {
                            'Authorization': `Basic ${TOKEN}`,
                        },
                    })
                        .then((res) => {
                            this.setState({
                                networkErrorAlert: false,
                                dataLoading: false,
                                dailyData: res.data.data
                            })
                        })
                        .catch((error) => {
                            this.setState({
                                networkErrorAlert: true,
                            })
                        })
                    /** end */

                    /** start get token balance */
                    axios.get(`${API_ROOT}` + '/api/tron/getTokenBalance?address=' + this.state.fromAddress, {
                        headers: {
                            'Authorization': `Basic ${TOKEN}`,
                        },
                    })
                        .then((tokenbalance) => {
                            this.setState({ tokenBalance: Number(tokenbalance.data.balance) })
                        })
                        .catch((errorbalance) => {
                            console.error(errorbalance)
                        })
                    /** end */


                    /** get contract address  */
                    axios.get(`${API_ROOT}` + '/api/tron/getContractAddress?address=' + this.state.fromAddress, {
                        headers: {
                            'Authorization': `Basic ${TOKEN}`,
                        },
                    })
                        .then((res) => {
                            // console.log("contract address", res.data.address);
                            this.setState({ contractAddress: res.data.address })
                        })
                        .catch((err) => {
                            console.log(err);
                        })
                }

                else {
                    this.setState({
                        hostError: true,
                    })
                }

            } else {
                console.log('still searching...')
            }
        }, `${INTERVAL}`)
    }


    // setting toAddress
    handleToAddress = (event) => {
        this.setState({
            toAddress: event.target.value,
        })
    }


    // setting sending amount
    handleSendingAmount = (event) => {

        if (Number(event.target.value) > Number(this.state.tokenBalance)) {
            return callErrorToast("Amount should not be more than avaliable balance.");
        } else {
            this.setState({
                sendingAmount: event.target.value,
            })
        }
    }


    // setting maximam amount 
    setMaxAmount = () => {
        this.setState({
            sendingAmount: this.state.tokenBalance
        })
    }


    // transfer amount function
    sendAmount = async () => {

        if (this.state.toAddress.length < 1) {
            return callErrorToast("Please Enter recipient address.");
        }

        if (Number(this.state.sendingAmount) <= 0) {
            return callErrorToast("Please Enter the amount more than 0.");
        }

        if (Number(this.state.sendingAmount) > Number(this.state.tokenBalance)) {
            return callErrorToast("Amount should not be more than avaliable balance.");
        }

        else {
            try {
                if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
                    const trc20ContractAddress = this.state.contractAddress; //contract address
                    let contract = await window.tronWeb.contract().at(trc20ContractAddress);
                    let result = await contract.transfer(this.state.toAddress, 1000000 * this.state.sendingAmount).send();// to address  // 0.0000001
                    // console.log('result: ', result);
                    this.setState({
                        hash: result,
                        modalIsOpen: true
                    });

                }
            } catch (err) {
                return callErrorToast("Transaction can not be processed!");
            }

        }
    }



    // modal function 
    openModal = () => {
        this.setState({ modalIsOpen: true });
    }


    closeModal = () => {
        this.setState({ modalIsOpen: false });
    }


    // copy function 
    copyCodeToClipboard = () => {
        const el = this.textArea
        el.select()
        document.execCommand("copy")
        return callSuccessToast("Copied Successfully!");
    }

    render() {

        return (
            <>
                {this.state.hostError ?
                    <div class="pageLoader d-flex align-items-center justify-content-center">
                        <div class="d-flex flex-column align-items-center">
                            <div class="sa">
                                <div class="sa-warning">
                                    <div class="sa-warning-body"></div>
                                    <div class="sa-warning-dot"></div>
                                </div>
                            </div>
                            <h5 class="text-center">Please change the network to {HOSTNETWORKNAME}.</h5>
                        </div>
                    </div>
                    : ""}

                <div className="main-content sidebar-open">

                    {this.state.networkErrorAlert ?
                        <ErrorAlert title="Error Retrieving Data !" />
                        : ""}

                    {window?.tronWeb?.defaultAddress?.base58 ? null : <WarningComponent />}
                    <div className="transform-wrap mb-39">
                        <div className="heading">
                            <h4>Transfer</h4>
                        </div>
                        <div className="card">
                            <div className="row">
                                <div className="col-lg-6 mx-auto">
                                    <div className="input-wrap">

                                        <div className="form-group">
                                            <input type="text" className="form-control" placeholder="From Your Tron Address" value={this.state.fromAddress} />
                                        </div>
                                        <div className="form-group">
                                            <input type="text" className="form-control" placeholder="To Recipient Address" value={this.state.toAddress} onChange={this.handleToAddress} />
                                        </div>
                                        <div className="input-group form-group">
                                            <input type="number" className="form-control" placeholder={this.state.tokenBalance || this.state.tokenBalance == 0 ? "Amount in RYZ available " + this.state.tokenBalance : "Fetching avaliable amount..."} aria-describedby="max" value={this.state.sendingAmount} onChange={this.handleSendingAmount} max={this.state.tokenBalance} />
                                            <div className="input-group-append">
                                                <span className="input-group-text" id="max">
                                                    <span className="prep-main" onClick={this.setMaxAmount}>MAX</span>
                                                    <span className="prep-head">RYZ </span>
                                                </span>
                                            </div>
                                        </div>
                                        <button type="button" className="btn primary-btn w-100 btn-lg" onClick={this.sendAmount}>SEND RYZ </button>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="table-wrap mb-39">
                        <div className="heading">
                            <h4>Daily Data</h4>
                        </div>
                        <div className="table-responsive empty-table card ">

                            <table className="table">
                                <thead>
                                    <tr>
                                        <th scope="col">Day</th>
                                        <th scope="col">Hash</th>
                                        <th scope="col">Address</th>
                                        <th scope="col">Amount</th>
                                    </tr>
                                </thead>
                                <tbody id="dailydata" className="position-realtive">
                                    {!this.state.dataLoading ?
                                        (this.state.dailyData.length > 0) ? this.state.dailyData.sort((a, b) => b.day - a.day, {}).map((item, index) => (
                                            <tr key={index}>
                                                <td>{new Date(item.day).toLocaleString()}</td>
                                                <td>{item.txid}</td>
                                                <td>{item.address}</td>
                                                <td>{item.amount}</td>
                                            </tr>
                                        )) : <tr><td className="no-data">No Data Found</td></tr>

                                        : <tr><td className="p-0">
                                            <div className="loader">
                                                <span></span>
                                                <span></span>
                                                <span></span>
                                                <span></span>
                                                <p className="pt-2 text-center">Loading Data from Blockchain... </p>
                                            </div></td></tr>}
                                </tbody>
                            </table>

                        </div>
                    </div>
                </div>

                <div>
                    <Modal
                        isOpen={this.state.modalIsOpen}
                        onRequestClose={this.closeModal}
                        style={customStyles}
                    >

                        <div className="modalContainer p-3 position-realtive">
                            <i aria-hidden="true" className="position-absolute modal-close" onClick={this.closeModal}>&times;</i>
                            <div className="alert alert-success text-center font-weight-bold mt-4" role="alert">
                                You request is executed on blockchain.
                            </div>
                            <div className="input-group mb-3">
                                <input type="text" className="form-control" placeholder="Hash" ref={(textarea) => this.textArea = textarea} value={this.state.hash} />
                                <div className="input-group-append">
                                    <span className="input-group-text d-flex align-items-center justify-content-center primary-btn px-3" id="basic-addon2" onClick={() => this.copyCodeToClipboard()}><span>
                                        <i className="material-icons input-icon align-middle mr-1">content_copy</i>Copy Hash</span></span>
                                </div>
                            </div>
                        </div>
                    </Modal>
                </div>

                <ButterToast position={{ vertical: POS_UP, horizontal: POS_RIGHT }} />
            </>

        )
    }
}