import React from 'react';
import ButterToast, { Cinnamon } from 'butter-toast';


export const callToast = (message) => {
    ButterToast.raise({
        content: <Cinnamon.Crisp scheme={Cinnamon.Crisp.SCHEME_BLUE}
            content={() => <div>{message}</div>}
            title="Notification" />
    });
}

export const callSuccessToast = (message) => {
    ButterToast.raise({
        content: <Cinnamon.Crisp scheme={Cinnamon.Crisp.SCHEME_GREEN}
            content={() => <div>{message}</div>}
            title="Notification" />
    });
}

export const callErrorToast = (message) => {
    ButterToast.raise({
        content: <Cinnamon.Crisp scheme={Cinnamon.Crisp.SCHEME_RED}
            content={() => <div>{message}</div>}
            title="Notification"
            timeout={50}
            />
           
    });
}