import React, { Component } from "react";
import { Link } from "react-router-dom";
import WarningComponent from "../../commoncomponent/warning.js";
import axios from "axios";
import ErrorAlert from "../../commoncomponent/ErrorAlert";
import { API_ROOT, TOKEN, INTERVAL, HOSTNETWORK, HOSTNETWORKNAME } from '../../config';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { callErrorToast, callSuccessToast } from '../../helper';
import ButterToast, { POS_UP, POS_BOTTOM, POS_RIGHT } from 'butter-toast';
import Modal from 'react-modal';


const customStyles = {
    content: {
        top: "50%",
        left: "50%",
        right: "auto",
        bottom: "auto",
        marginRight: "-50%",
        transform: "translate(-50%, -50%)"
    }
};


export default class Stake extends Component {
    constructor() {
        super();
        this.state = {
            fromAddress: null,
            activeStakes: [],
            historyStakes: [],
            errorMsgActive: null,
            errorMsgHistory: null,
            dataLoading: true,
            stakeDate: new Date().setDate(new Date().getDate() + 1),
            tokenbalance: null,
            stakeAmount: '',
            contractAddress: '',
            hash: '',
            modalIsOpen: false,
            stakeData: '',
            stakeDetails: [],
            stakeWithdraModalOpen: false,
            stakeWithdrawhashOpen: false,
            stakeID: '',
            stakeWithdrawHash: '',
            loadingStakeDetails: false,
            hostError: false,
        }
    }


    componentDidMount() {
        setInterval(() => {

            if (window.tronWeb && window.tronWeb.defaultAddress && window.tronWeb.defaultAddress.base58) {
                this.setState({
                    fromAddress: window.tronWeb.defaultAddress.base58
                })

                if (window.tronWeb && window.tronWeb.defaultAddress && window.tronWeb.solidityNode.host == HOSTNETWORK) {
                    this.setState({
                        hostError: false,
                    })

                    /** start get token balance */
                    axios.get(`${API_ROOT}` + '/api/tron/getTokenBalance?address=' + this.state.fromAddress, {
                        headers: {
                            'Authorization': `Basic ${TOKEN}`,
                        },
                    })
                        .then((res) => {

                            this.setState({ tokenBalance: Number(res.data.balance) })
                        })
                        .catch((err) => {
                            console.error(err)
                        })
                    /** end */


                    /** Active stakes */
                    axios.get(`${API_ROOT}` + '/api/tron/getActiveStakesByUserAddress?address=' + this.state.fromAddress, {
                        headers: {
                            'Authorization': `Basic ${TOKEN}`,
                        },
                    })
                        .then((resActiveStakes) => {
                            // console.log("active Stakes", resActiveStakes);
                            this.setState({
                                activeStakes: resActiveStakes.data,
                                dataLoading: false,
                                networkErrorAlert: false,
                            }, () => console.log("state", this.state.activeStakes))
                        })
                        .catch((errorActiveStakes) => {
                            console.log(errorActiveStakes);
                            // callErrorToast("Error retreiving data");
                            this.setState({
                                networkErrorAlert: true,
                            })
                        })
                    /** end */


                    /** History Stakes */
                    axios.get(`${API_ROOT}` + '/api/tron/getStakeHistoryByUserAddress?address=' + this.state.fromAddress, {
                        headers: {
                            'Authorization': `Basic ${TOKEN}`,
                        },
                    })
                        .then((resHistoryStakes) => {
                            // console.log("history stakes", resHistoryStakes);
                            this.setState({ historyStakes: resHistoryStakes.data })
                        })
                        .catch((errorHistoryStakes) => {
                            console.log(errorHistoryStakes);
                        })
                    /** end */

                    /** get contract address  */
                    axios.get(`${API_ROOT}` + '/api/tron/getContractAddress?address=' + this.state.fromAddress, {
                        headers: {
                            'Authorization': `Basic ${TOKEN}`,
                        },
                    })
                        .then((res) => {
                            this.setState({ contractAddress: res.data.address })
                        })
                        .catch((err) => {
                            console.log(err);
                        })

                }
                else {
                    this.setState({
                        hostError: true,
                    })
                }
            } else {
                console.log('still searching....')
            }

        }, `${INTERVAL}`)

    }


    // setting date in picker 
    handleDateChange = (date) => {
        this.setState({
            stakeDate: date,
        })
    }

    // setting sending amount
    handleSendingAmount = (event) => {

        if (Number(event.target.value) > Number(this.state.tokenBalance)) {
            return callErrorToast("Amount should not be more than avaliable balance.");
        } else {
            this.setState({
                stakeAmount: event.target.value,
            })
        }
    }


    // setting maximam amount 
    setMaxAmount = () => {
        this.setState({
            stakeAmount: this.state.tokenBalance
        })
    }


    handleStake = async () => {
        // converting date into unix timestamp


        if (Number(this.state.stakeAmount) < 2) {
            return callErrorToast("Please Enter the amount more than 1.");
        }

        if (!this.state.stakeDate) {
            return callErrorToast("Please select the date.");
        }

        if (!this.state.stakeDate) {
            return callErrorToast("Please select the date.");
        }

        if (Number(this.state.stakeAmount) > Number(this.state.tokenBalance)) {
            return callErrorToast("Amount should not be more than avaliable balance.");
        }

        else {
            let stakeDate = parseInt(new Date(this.state.stakeDate).getTime() / 1000);

            try {
                if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
                    const trc20ContractAddress = this.state.contractAddress //contract address
                    let contract = await window.tronWeb.contract().at(trc20ContractAddress);
                    let result = await contract.performStaking(this.state.stakeAmount * 1000000, stakeDate).send();// to address  // 0.0000001 //6 decimal limit 
                    this.setState({
                        modalIsOpen: true,
                        hash: result,
                    });
                }

            } catch (err) {
                return callErrorToast("Request can not be processed!");
            }

        }
    }

    // modal function 
    openModal = () => {
        this.setState({ modalIsOpen: true });
    }


    closeModal = () => {
        this.setState({ modalIsOpen: false });
    }


    closeStakeWithdrawModal = () => {
        this.setState({ stakeWithdraModalOpen: false });
    }

    closestakeWithdrawHash = () => {
        this.setState({ stakeWithdrawhashOpen: false });
    }

    // copy function 
    copyCodeToClipboard = () => {
        const el = this.textArea
        el.select()
        document.execCommand("copy")
        return callSuccessToast("Copied Successfully!");
    }


    // check stake

    handleCheckStake = async () => {
        this.setState({
            loadingStakeDetails: true,
        });



        if (Number(this.state.stakeAmount) < 2) {
            return callErrorToast("Please Enter the amount more than 1.");
        }

        if (!this.state.stakeDate) {
            return callErrorToast("Please select the date.");
        }

        let stakeDate = parseInt(new Date(this.state.stakeDate).getTime() / 1000);

        /** get contract address  */
        await axios.get(`${API_ROOT}/api/tron/getStakeDetails?endTime=${stakeDate}&amount=${this.state.stakeAmount}`, {
            headers: {
                'Authorization': `Basic ${TOKEN}`,
            },
        })
            .then((res) => {
                // console.log(res);
                this.setState({
                    loadingStakeDetails: false,
                    stakeDetails: res.data,
                })
            })
            .catch((err) => {
                this.setState({
                    loadingStakeDetails: false,
                })
                console.error(err)
            })
    }



    getStakeId = (stakeId, index) => {
        this.setState({
            stakeID: stakeId,
            stakeWithdraModalOpen: true,
        })
    }


    // withdraw stake
    handleWithdrawSkate = async () => {

        document.getElementById(this.state.stakeID).disabled = true;

        try {
            if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
                const trc20ContractAddress = this.state.contractAddress; //contract address
                let contract = await window.tronWeb.contract().at(trc20ContractAddress);
                let result = await contract.withdrawStakedTokens(this.state.stakeID).send();

                this.setState({
                    stakeWithdrawHash: result,
                    stakeWithdraModalOpen: false,
                    stakeWithdrawhashOpen: true,
                });

            }

        } catch (err) {
            this.setState({
                stakeWithdraModalOpen: false,
            })
            document.getElementById(this.state.stakeID).disabled = false;
            return callErrorToast("Withdraw can not be processed!");
        }
    }


    render() {
        return (
            <React.Fragment>
                {this.state.hostError ?
                    <div class="pageLoader d-flex align-items-center justify-content-center">
                        <div class="d-flex flex-column align-items-center">
                            <div class="sa">
                                <div class="sa-warning">
                                    <div class="sa-warning-body"></div>
                                    <div class="sa-warning-dot"></div>
                                </div>
                            </div>
                            <h5 class="text-center">Please change the network to {HOSTNETWORKNAME}.</h5>
                        </div>
                    </div>
                    : ""}
                <div className="main-content sidebar-open stake-page">
                    {this.state.networkErrorAlert ?
                        <ErrorAlert title="Error Retrieving Data !" />
                        : ""}
                    {window?.tronWeb?.defaultAddress?.base58 ? null : <WarningComponent />}
                    <div className="stake-wrap mb-39">
                        <div className="heading">
                            <h4>Stake</h4>
                        </div>
                        <div className="card stake-box">
                            <div className="row">
                                <div className="col-xl-7 mx-auto">
                                    <div className="input-wrap">

                                        <div className="input-group mb-39 flex-nowrap">
                                            <input type="number" className="form-control" placeholder={this.state.tokenBalance || this.state.tokenBalance == 0 ? "Amount in RYZ available " + this.state.tokenBalance : "Fetching avaliable amount..."} aria-describedby="max" value={this.state.stakeAmount} onChange={this.handleSendingAmount} />
                                            <div className="input-group-append">
                                                <span className="input-group-text" id="max">
                                                    <span className="prep-main" onClick={this.setMaxAmount}>MAX</span>
                                                    <span className="prep-head">RYZ</span>
                                                </span>
                                            </div>
                                        </div>
                                        <div className="input-group mb-39 flex-nowrap">
                                            <DatePicker
                                                selected={this.state.stakeDate}
                                                onChange={this.handleDateChange}
                                                onCalendarClose={this.handleCalendarClose}
                                                onCalendarOpen={this.handleCalendarOpen}
                                                minDate={new Date().setDate(new Date().getDate() + 1)}
                                            />

                                            <div className="input-group-append">
                                                <span className="input-group-text" id="days">
                                                    <span className="prep-main">
                                                        <i className="material-icons align-middle">
                                                            event
                                                            </i>
                                                    </span>
                                                    <span className="prep-head">Days</span>
                                                </span>
                                            </div>

                                        </div>

                                        <button className="btn dark-btn w-100 btn-lg mb-4" onClick={this.handleCheckStake}>CHECK STAKE</button>
                                        <button className="btn primary-btn w-100 btn-lg" onClick={this.handleStake}>STAKE</button>

                                    </div>
                                </div>
                                <div className="col-xl-5">
                                    <div className="stakes-content">
                                        <p class="text-success-c f-14">
                                            * Click check stake button to view your stake details.
                                        </p>

                                        {this.state.loadingStakeDetails ? <p className="blink-soft text-warning font-weight-bolder">Featching Data from Blockchain... </p> : ""}

                                        <ul className="stakes-list">
                                            <li>
                                                <div className="d-flex justify-content-between mb-2">
                                                    <span className="data-title">Bonus Amount:</span>
                                                    <span className="data-item">{this.state.stakeDetails && this.state.stakeDetails.stakeBonus ? this.state.stakeDetails.stakeBonus : "0.000"} RYZ</span>
                                                </div>
                                            </li>
                                            <li>
                                                <div className="d-flex justify-content-between mb-2">
                                                    <span className="data-title">Effective Amount:</span>
                                                    <span className="data-item">{this.state.stakeDetails && this.state.stakeDetails.finalAmount ? this.state.stakeDetails.finalAmount : "0.000"} RYZ</span>
                                                </div>
                                            </li>
                                            <li className="mt-4">
                                                <div className="d-flex justify-content-between mb-2">
                                                    <span className="data-title">Stake Rate:</span>
                                                    <span className="data-item">{this.state.stakeDetails && this.state.stakeDetails.finalAmount ? this.state.stakeDetails.stakeRate : "0.000"}/RYZ</span>
                                                </div>
                                            </li>
                                            <li>
                                                <div className="d-flex justify-content-between mb-2">
                                                    <span className="data-title">Stake Share:</span>
                                                    <span className="data-item">{this.state.stakeDetails && this.state.stakeDetails.stakeShares ? this.state.stakeDetails.stakeShares : "0.000"} RYZ</span>
                                                </div>
                                            </li>

                                            <li className="mt-4">
                                                <div className="d-flex justify-content-between mb-2">
                                                    <span className="data-title">Big Pay Day Bonus: </span>
                                                    <span className="data-item">{this.state.stakeDetails && this.state.stakeDetails.bigPayDay ? this.state.stakeDetails.bigPayDay : "0.000"} RYZ</span>
                                                </div>
                                                <p class="mt-2 toolTipText">* If stake is still open on BigPayDay</p>
                                            </li>

                                            <li className="mt-4">
                                                <div className="d-flex justify-content-between mb-2">
                                                    <span className="data-title">Start Date:</span>
                                                    <span className="data-item">{this.state.stakeDetails && this.state.stakeDetails.startDay ? this.state.stakeDetails.startDay : 0}</span>
                                                </div>
                                            </li>
                                            <li>
                                                <div className="d-flex justify-content-between mb-2">
                                                    <span className="data-title">Last Share:</span>
                                                    <span className="data-item">{this.state.stakeDetails && this.state.stakeDetails.endDay ? this.state.stakeDetails.lastDay : 0}</span>
                                                </div>
                                            </li>
                                            <li>
                                                <div className="d-flex justify-content-between mb-2">
                                                    <span className="data-title">End Date:</span>
                                                    <span className="data-item">{this.state.stakeDetails && this.state.stakeDetails.stakeBonus ? this.state.stakeDetails.endDay : 0}</span>
                                                </div>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="table-wrap mb-39">
                        <div className="heading">
                            <h4>Active Stakes</h4>
                        </div>
                        <div className="table-responsive empty-table card ">
                            <table className="table">
                                <thead>
                                    <tr>
                                        <th scope="col">
                                            Start Date
                                        </th>
                                        <th scope="col">End Date</th>
                                        <th scope="col">Principal</th>
                                        <th scope="col">Shares</th>
                                        <th scope="col">BigPayDay
                                        <Link to="">
                                                <img className="img-fluid ml-1" src="/images/info-icon.png" alt="info-icon" height="14" width="14" />
                                            </Link>
                                        </th>
                                        <th scope="col">Interest</th>
                                        <th scope="col">Final Value</th>
                                        <th scope="col"></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {!this.state.dataLoading ?
                                        (this.state.activeStakes && this.state.activeStakes.length > 0) ? this.state.activeStakes.sort((a, b) => a.StakeId - b.StakeId, {}).map((item, index) => (
                                            <tr key={index}>
                                                <td>{new Date(item.StakingStartTime * 1000).toLocaleDateString()}</td>
                                                <td>{new Date(item.StakingEndTime * 1000).toLocaleDateString()}</td>
                                                <td>{item.StakerTokens}</td>
                                                <td>{item.Shares}</td>
                                                <td>{item.BigPayDay}</td>
                                                <td>{item.Interest} %</td>
                                                <td>{item.Amount}</td>
                                                <td>{item.TokenTransactionstatus == "false" || !item.TokenTransactionstatus ?
                                                    <button type="button" className="btn-custom-grey" onClick={() => this.getStakeId(item.StakeId)} id={item.StakeId}>Withdraw</button>
                                                    : <span className="text-danger-c">Stake Closed</span>}</td>
                                            </tr>
                                        )) : <tr><td className="no-data">No Data Found</td></tr>
                                        : <tr><td className="p-0">
                                            <div className="loader">
                                                <span></span>
                                                <span></span>
                                                <span></span>
                                                <span></span>
                                                <p className="pt-2 text-center">Loading Data from Blockchain... </p>
                                            </div></td></tr>}
                                </tbody>
                            </table>

                        </div>
                    </div>

                    <div className="table-wrap mb-39">
                        <div className="heading">
                            <h4>Stakes History</h4>
                        </div>
                        <div className="table-responsive empty-table card ">

                            <table className="table">
                                <thead>
                                    <tr>
                                        <th scope="col">
                                            Start Date
                                        </th>
                                        <th scope="col">End Date</th>
                                        <th scope="col">Principal</th>
                                        <th scope="col">Shares</th>
                                        <th scope="col">BigPayDay
                                        <Link to="">
                                                <img className="img-fluid ml-1" src="/images/info-icon.png" alt="info-icon" height="14" width="14" />
                                            </Link> </th>
                                        <th scope="col">Interest</th>
                                        <th scope="col">Final Value</th>
                                        <th scope="col"></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {!this.state.dataLoading ?
                                        (this.state.historyStakes && this.state.historyStakes.length > 0) ? this.state.historyStakes.sort((a, b) => a.StakeId - b.StakeId, {}).map((item, index) => (
                                            <tr key={index}>
                                                <td>{new Date(item.StakingStartTime * 1000).toLocaleDateString()}</td>
                                                <td>{new Date(item.StakingEndTime * 1000).toLocaleDateString()}</td>
                                                <td>{item.StakerTokens}</td>
                                                <td>{item.Shares}</td>
                                                <td>{item.BigPayDay}</td>
                                                <td>{item.Interest} %</td>
                                                <td>{item.Amount}</td>
                                                <td>{item.TokenTransactionstatus == "false" || !item.TokenTransactionstatus ?
                                                    <button type="button" className="btn-custom-grey" onClick={() => this.getStakeId(item.StakeId)} id={item.StakeId}>Withdraw</button>
                                                    : <span className="text-danger-c">Stake Closed</span>}</td>

                                            </tr>
                                        )) : <tr><td className="no-data">No Data Found</td></tr>
                                        : <tr><td className="p-0">
                                            <div className="loader">
                                                <span></span>
                                                <span></span>
                                                <span></span>
                                                <span></span>
                                                <p className="pt-2 text-center">Loading Data from Blockchain... </p>
                                            </div></td></tr>}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>


                {/* stake request successfull hash modal */}
                <div>
                    <Modal
                        isOpen={this.state.modalIsOpen}
                        onRequestClose={this.closeModal}
                        style={customStyles}
                    >

                        <div className="modalContainer p-3 position-realtive">
                            <i aria-hidden="true" className="position-absolute modal-close" onClick={this.closeModal}>&times;</i>

                            <div className="alert alert-success text-center font-weight-bold mt-4" role="alert">
                                You request is executed on blockchain.
                            </div>
                            <div className="input-group mt-4 mb-3">
                                <input type="text" className="form-control" placeholder="Hash" ref={(textarea) => this.textArea = textarea} value={this.state.hash} />
                                <div className="input-group-append">
                                    <span className="input-group-text d-flex align-items-center justify-content-center primary-btn px-3" id="basic-addon2" onClick={() => this.copyCodeToClipboard()}>
                                        <span><i className="material-icons input-icon align-middle mr-1">content_copy</i> Copy Hash</span></span>
                                </div>
                            </div>
                        </div>
                    </Modal>
                </div>


                {/* withdraw stake amount modal */}
                <div>
                    <Modal
                        isOpen={this.state.stakeWithdraModalOpen}
                        onRequestClose={this.closeStakeWithdrawModal}
                        style={customStyles}
                    >

                        <div className="p-4">
                            <i aria-hidden="true" className="position-absolute modal-close p-2" onClick={this.closeStakeWithdrawModal}>&times;</i>
                            <h2 className="mt-3 text-center">Are you sure to withdraw this stake ?</h2>

                            <div className="input-wrap mt-4 d-flex ">
                                <button type="button" className="btn dark-btn w-100 btn-lg mt-3 mx-2" onClick={this.closeStakeWithdrawModal}>No</button>
                                <button type="button" className="btn primary-btn w-100 btn-lg mt-3 mx-2" onClick={this.handleWithdrawSkate}>Yes</button>
                            </div>

                            <div class="alert alert-warning text-center mt-4 mx-2" role="alert">
                                *Penalty may be applicable based on time.
                            </div>
                        </div>
                    </Modal>
                </div>


                {/* stake withdraw hash modal */}

                <div>
                    <Modal
                        isOpen={this.state.stakeWithdrawhashOpen}
                        onRequestClose={this.closestakeWithdrawHash}
                        style={customStyles}
                    >

                        <div className="modalContainer p-3 position-realtive">
                            <i aria-hidden="true" className="position-absolute modal-close" onClick={this.closestakeWithdrawHash}>&times;</i>

                            <div className="alert alert-success text-center font-weight-bold mt-4" role="alert">
                                You request is executed on blockchain.
                            </div>
                            <div className="input-group mt-4 mb-3">
                                <input type="text" className="form-control" placeholder="Hash" ref={(textarea) => this.textArea = textarea} value={this.state.stakeWithdrawHash} />
                                <div className="input-group-append">
                                    <span className="input-group-text d-flex align-items-center justify-content-center primary-btn px-3" id="basic-addon2" onClick={() => this.copyCodeToClipboard()}>
                                        <span><i className="material-icons input-icon align-middle mr-1">content_copy</i> Copy Hash</span></span>
                                </div>
                            </div>
                        </div>
                    </Modal>
                </div>
                <ButterToast position={{ vertical: POS_UP, horizontal: POS_RIGHT }} />
            </React.Fragment>
        )
    }
}
