import React, { Component } from 'react';
import { Link } from "react-router-dom";

export default class Warning extends Component {
    render(){
        return(
            <div className="alert-box d-flex justify-content-between flex-wrap">
                <span>
                    <i className="material-icons align-middle alert-icon">warning</i>
                    RYZ requires the <a target="_blank" href="https://chrome.google.com/webstore/detail/tronlink/ibnejdfjmmkpcnlpebklmnkoeoihofec" rel="noopener noreferrer" style={{color:'#ffbf00'}}>Tronlink extension</a> for Chrome to access the Tron network.
                </span>
                <Link to="#">
                    <img className="img-fluid" src="/images/icon.png" alt="icon" />
                </Link>
            </div>
        );
    }
}