import React, { Component } from "react";
import WarningComponent from "../../commoncomponent/warning";
import axios from "axios";
import ErrorAlert from "../../commoncomponent/ErrorAlert";
import { API_ROOT, TOKEN, INTERVAL, HOSTNETWORK, HOSTNETWORKNAME } from '../../config';
import Modal from 'react-modal';
import { callErrorToast, callSuccessToast } from '../../helper';
import ButterToast, { POS_UP, POS_BOTTOM, POS_RIGHT } from 'butter-toast';

const customStyles = {
    content: {
        top: "50%",
        left: "50%",
        right: "auto",
        bottom: "auto",
        marginRight: "-50%",
        transform: "translate(-50%, -50%)"
    }
};

export default class Refer extends Component {
    constructor() {
        super();
        this.state = {
            fromAddress: null,
            getRefferLink: null,
            errorMsgRefferLink: null,
            refferralHistory: [],
            errorMsgRefferHistory: null,
            dataLoading: true,
            withdraw: null,
            withdrawStatus: null,
            errorMsgWithdraw: null,
            modalIsOpen: false,
            withdrawMsg: '',
            contractAddress: '',
            referralWithdrawHash: '',
            referralAddress: '',
            hostError: false,
        }
    }


    generateRefferLink() {
        /** start generate the referal link */
        axios.get(`${API_ROOT}` + '/api/tron/getReferalLinkByAddress?address=' + this.state.fromAddress, {
            headers: {
                'Authorization': `Basic ${TOKEN}`,
            },
        })
            .then((resRefferLink) => {
                this.setState({ getRefferLink: resRefferLink.data.referralLink })
            })
            .catch((errorRefferLink) => {
                console.log(errorRefferLink);
                // this.setState({ errorMsgRefferLink: "Error retreiving data" })
            })
        /** end */
    }


    copyCodeToClipboard = () => {
        const el = this.input
        el.select()
        document.execCommand("copy")
        return callSuccessToast("Copied Successfully!");
    }


    /** end */
    componentDidMount() {
        setInterval(() => {

            if (window.tronWeb && window.tronWeb.defaultAddress && window.tronWeb.defaultAddress.base58) {
                this.setState({
                    fromAddress: window.tronWeb.defaultAddress.base58
                })

                if (window.tronWeb && window.tronWeb.defaultAddress && window.tronWeb.solidityNode.host == HOSTNETWORK) {
                    this.setState({
                        hostError: false,
                    })

                    /** start refferal history */
                    axios.get(`${API_ROOT}` + '/api/tron/getReferralHistory?address=' + this.state.fromAddress, {
                        headers: {
                            'Authorization': `Basic ${TOKEN}`,
                        },
                    })
                        .then((resRefferHistory) => {
                            this.setState({
                                networkErrorAlert: false,
                                refferralHistory: resRefferHistory.data.response,
                                dataLoading: false,
                            })
                        })
                        .catch((errorRefferHistory) => {
                            this.setState({
                                networkErrorAlert: true,
                            })
                        })
                    /** end */


                    /** get contract address  */
                    axios.get(`${API_ROOT}` + '/api/tron/getContractAddress?address=' + this.state.fromAddress, {
                        headers: {
                            'Authorization': `Basic ${TOKEN}`,
                        },
                    })
                        .then((res) => {
                            // console.log("contract address", res.data.address);
                            this.setState({ contractAddress: res.data.address })
                        })
                        .catch((err) => {
                            console.log(err);
                        })
                } else {
                    this.setState({
                        hostError: true,
                    })
                }
            } else {
                console.log('still searching ...')
            }

        }, `${INTERVAL}`)
    }



    // modal function 
    openModal = () => {
        this.setState({ modalIsOpen: true });
    }


    closeModal = () => {
        this.setState({ modalIsOpen: false });
    }


    closeReferralWithDraw = () => {
        this.setState({ referralWithDrawOpen: false });
    }

    /** Withdraw refferal */
    withdrawReferral = async () => {

        console.log(this.state.referralAddress);

        if (!this.state.withdraw) {
            return callErrorToast("Please enter or copy refer link.")
        }

        const data = {
            address: this.state.fromAddress,
            referralLink: this.state.withdraw
        }

        await axios.post(`${API_ROOT}` + '/api/tron/withdrawReferral', data, {
            headers: {
                'Authorization': `Basic ${TOKEN}`,
            },
        }).then((res) => {
            // console.log(res)
            this.setState({
                withdrawStatus: res.data.status,
                withdrawMsg: res.data.response,
                referralAddress: res.data.ReferralAddress,
            }, () => {
                if (this.state.withdrawStatus) {
                    this.getReferral();
                } else {
                    callErrorToast(res.data.response);
                }
            })
        }).catch((err) => {
            console.log("Invalid referral link.");
        })

    }


    // get referral withdraw
    getReferral = async () => {
        try {
            if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
                const trc20ContractAddress = this.state.contractAddress //contract address
                let contract = await window.tronWeb.contract().at(trc20ContractAddress);
                let result = await contract.withdrawReferral(this.state.referralAddress).send();

                console.log(result);

                this.setState({
                    referralWithdrawHash: result,
                    modalIsOpen: false,
                    referralWithDrawOpen: true,
                })
            }

        } catch (err) {
            return callErrorToast("Request can not be processed!");
        }
    }


    render() {
        return (
            <React.Fragment>
                {this.state.hostError ?
                    <div class="pageLoader d-flex align-items-center justify-content-center">
                        <div class="d-flex flex-column align-items-center">
                            <div class="sa">
                                <div class="sa-warning">
                                    <div class="sa-warning-body"></div>
                                    <div class="sa-warning-dot"></div>
                                </div>
                            </div>
                            <h5 class="text-center">Please change the network to {HOSTNETWORKNAME}.</h5>
                        </div>
                    </div>
                    : ""}
                <div className="main-content sidebar-open refer-page">
                    {this.state.networkErrorAlert ?
                        <ErrorAlert title="Error Retrieving Data !" />
                        : ""}
                    {window?.tronWeb?.defaultAddress?.base58 ? null : <WarningComponent />}
                    <div className="refer-wrap mb-39">
                        <div className="heading">
                            <h4>Refer</h4>
                        </div>
                        <div className="card">
                            <div className="content text-center mb-39">
                                <h4>Generate referral link and share with friend to earn more rewards.
                                <span className="d-block mt-2">One link is applicable for one user only !!</span>
                                </h4>
                            </div>

                            <div className="container">
                                <div className="row">
                                    <div className="col-xl-7 mx-auto">
                                        <div className="input-wrap">
                                            <div className="input-group">
                                                <input type="text" className="form-control" placeholder="Your RYZ Referral" ref={(input) => this.input = input} defaultValue={this.state.getRefferLink} />
                                                <div className="input-group-append">
                                                    <span onClick={() => this.copyCodeToClipboard()} className="input-group-text primary-color flex-row" id="input-id">
                                                        <i className="material-icons input-icon align-middle">
                                                            content_copy
                                            </i>
                                                    </span>
                                                </div>
                                            </div>
                                            <button type="button" onClick={() => this.generateRefferLink()} className="btn primary-btn w-100 btn-lg text-uppercase" style={{ marginTop: 30 }}>Generate Referral Link</button>
                                            <span className="d-block text-center mt-3">First time visitor, Already having referral Link ?  <a className="text-warning cursor-pointer" onClick={this.openModal}>Withdraw Here</a></span>
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="table-wrap mb-39">
                        <div className="heading">
                            <h4>Referral  History</h4>
                        </div>
                        <div className="table-responsive empty-table card ">
                            <table className="table">
                                <thead>
                                    <tr>
                                        <th scope="col">Day</th>
                                        <th scope="col">Referring TRX Address</th>
                                        <th scope="col">Detail</th>
                                        <th scope="col">Awarded RYZ</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {!this.state.dataLoading ?
                                        (this.state.refferralHistory && this.state.refferralHistory.length > 0) ? this.state.refferralHistory.map((item, index) => (
                                            <tr key={index}>
                                                <td>{item.Date}</td>
                                                <td>{item.UserAddress}</td>
                                                <td>{item.Details}</td>
                                                <td>{item.Amount}</td>
                                            </tr>
                                        )) : <tr><td className="no-data">No Data Found</td></tr>
                                        : <tr><td className="p-0">
                                            <div className="loader">
                                                <span></span>
                                                <span></span>
                                                <span></span>
                                                <span></span>
                                                <p className="pt-2 text-center">Loading Data from Blockchain... </p>
                                            </div></td></tr>}
                                </tbody>
                            </table>

                        </div>
                    </div>
                </div>


                {/* already having referral link modal */}
                <div>
                    <Modal
                        isOpen={this.state.modalIsOpen}
                        onRequestClose={this.closeModal}
                        style={customStyles}
                    >
                        <div className="p-4">
                            <i aria-hidden="true" className="position-absolute modal-close p-2" onClick={this.closeModal}>&times;</i>
                            <h2 className="mt-3 text-center">Withdraw Referral</h2>
                            <div className="input-wrap mt-4 pt-1">
                                <div className="input-group">
                                    <input
                                        type="text"
                                        className="form-control"
                                        placeholder="Paste referral link here"
                                        onChange={(e) => this.setState({ withdraw: e.target.value })}
                                    />
                                </div>

                                <button type="button" onClick={() => this.withdrawReferral()} className="btn primary-btn w-100 btn-lg" style={{ marginTop: 30 }}>Withdraw Referral</button>
                            </div>
                        </div>
                    </Modal>
                </div>

                {/* referral withdraw hash modal */}
                <div>
                    <Modal
                        isOpen={this.state.referralWithDrawOpen}
                        onRequestClose={this.closeReferralWithDraw}
                        style={customStyles}
                    >

                        <div className="modalContainer p-3 position-realtive">
                            <i aria-hidden="true" className="position-absolute modal-close" onClick={this.closeReferralWithDraw}>&times;</i>

                            <div className="alert alert-success text-center font-weight-bold mt-4" role="alert">
                                You request is executed on blockchain.
                            </div>
                            <div className="input-group mt-4 mb-3">
                                <input type="text" className="form-control" placeholder="Hash" ref={(input) => this.input = input} defaultValue={this.state.referralWithdrawHash} />

                                <div className="input-group-append">
                                    <span className="input-group-text d-flex align-items-center justify-content-center primary-btn px-3" id="basic-addon2" onClick={() => this.copyCodeToClipboard()}>
                                        <span><i className="material-icons input-icon align-middle mr-1">content_copy</i> Copy Hash</span></span>
                                </div>
                            </div>
                        </div>
                    </Modal>
                </div>

                <ButterToast position={{ vertical: POS_UP, horizontal: POS_RIGHT }} />
            </React.Fragment>
        )
    }
}