import React, { Component } from "react";
import WarningComponent from "../../commoncomponent/warning";
import axios from "axios";
import { API_ROOT, TOKEN, INTERVAL, HOSTNETWORK, HOSTNETWORKNAME } from '../../config';
import { callSuccessToast, callErrorToast } from '../../helper';
import ButterToast, { POS_UP, POS_RIGHT } from 'butter-toast';
import Modal from 'react-modal';
import ErrorAlert from "../../commoncomponent/ErrorAlert";


const customStyles = {
    content: {
        top: "50%",
        left: "50%",
        right: "auto",
        bottom: "auto",
        marginRight: "-50%",
        transform: "translate(-50%, -50%)",
    }
};


export default class Claim extends Component {
    constructor() {
        super();
        this.state = {
            fromAddress: '',
            btcAddress: '',
            checkBtcMessage: '',
            checkBtcStatus: null,
            btcRewards: null,
            dataLoading: true,
            generateModalOpen: false,
            uniqueString: '',
            claimValue: '',
            btcBalance: '',
            contractAddress: '',
            claimHashOpen: false,
            claimHash: '',
            hostError: false,
        }
    }


    componentDidMount() {
        setInterval(() => {

            if (window.tronWeb && window.tronWeb.defaultAddress && window.tronWeb.defaultAddress.base58) {

                this.setState({
                    fromAddress: window.tronWeb.defaultAddress.base58
                })

                if (window.tronWeb && window.tronWeb.defaultAddress && window.tronWeb.solidityNode.host == HOSTNETWORK) {
                    this.setState({
                        hostError: false,
                    })

                    /** start get daily data based on the address */
                    axios.get(`${API_ROOT}` + '/api/tron/getAllBTCRewardsByAddress?address=' + this.state.fromAddress, {
                        headers: {
                            'Authorization': `Basic ${TOKEN}`,
                        },
                    })
                        .then((res) => {
                            this.setState({
                                btcRewards: res.data,
                                dataLoading: false,
                                networkErrorAlert: false,
                            })
                        })
                        .catch((error) => {
                            this.setState({
                                networkErrorAlert: true,
                            })
                        })
                    /** end */


                    /** get contract address  */
                    axios.get(`${API_ROOT}` + '/api/tron/getContractAddress?address=' + this.state.fromAddress, {
                        headers: {
                            'Authorization': `Basic ${TOKEN}`,
                        },
                    })
                        .then((res) => {
                            this.setState({ contractAddress: res.data.address })
                        })
                        .catch((err) => {
                            console.log(err);
                        })


                }
                else {
                    this.setState({
                        hostError: true,
                    })
                }

            } else {
                console.log('still searching ...')
            }

        }, `${INTERVAL}`)

    }


    // getting btc value
    handleAddress = (event) => {
        this.setState({
            btcAddress: event.target.value,
        })
    }


    // check bitcoin address
    checkbtcAddress = () => {
        if (!this.state.btcAddress) {
            return callErrorToast("Please Enter BTC Address");
        }

        else {
            axios.get(`${API_ROOT}` + '/api/tron/checkBTCAddress?address=' + this.state.btcAddress, {
                headers: {
                    'Authorization': `Basic ${TOKEN}`,
                },
            })
                .then((res) => {
                    this.setState({
                        checkBtcMessage: res.data.message,
                        checkBtcStatus: res.data.status
                    })
                })
                .catch((error) => {
                    console.log(error);
                })
            /** end */
        }
    }


    // generate unique key string

    generateUniqeString = () => {
        try {
            if (window.tronWeb && window.tronWeb.defaultAddress && window.tronWeb.defaultAddress.base58) {
                axios.get(`${API_ROOT}` + '/api/tron/genrateBitcoinMessage?address=' + this.state.btcAddress, {
                    headers: {
                        'Authorization': `Basic ${TOKEN}`,
                    },
                })
                    .then((res) => {
                        this.setState({
                            uniqueString: res.data.message,
                        })
                    })
                    .catch((error) => {
                        console.log(error);
                    })
            }

        } catch (err) {
            return callErrorToast("Something went wrong.")
        }
    }



    // modal function 
    openGenerateModal = () => {
        this.setState({ generateModalOpen: true });
    }


    closeGenerateModal = () => {
        this.setState({ generateModalOpen: false });
    }

    closeClaimHashModal = () => {
        this.setState({ claimHashOpen: false });
    }


    // copy function 
    copyCodeToClipboard = () => {
        const el = this.textArea
        el.select()
        document.execCommand("copy")
        return callSuccessToast("Copied Successfully!");
    }


    handleClaimValue = (event) => {
        this.setState({
            claimValue: event.target.value,
        })
    }


    // get btc balance  

    getBtcBalance = () => {
        if (window.tronWeb && window.tronWeb.defaultAddress && window.tronWeb.defaultAddress.base58) {
            /** start get daily data based on the address */
            axios.get(`${API_ROOT}` + '/api/tron/getBTCBalance?address=' + this.state.btcAddress, {
                headers: {
                    'Authorization': `Basic ${TOKEN}`,
                },
            })
                .then((res) => {
                    this.setState({
                        btcBalance: res.data.message,
                    }, () => this.claimBonus())
                })
                .catch((error) => {
                    console.error(error)
                })
        }
        /** end */
    }


    // handle ryz
    handleClaimRyz = () => {
        try {
            if (window.tronWeb && window.tronWeb.defaultAddress && window.tronWeb.defaultAddress.base58) {
                axios.get(`${API_ROOT}/api/tron/validateBitcoinSignature?signature=${this.state.claimValue}&address=${this.state.btcAddress}`, {
                    headers: {
                        'Authorization': `Basic ${TOKEN}`,
                    },
                })
                    .then((res) => {
                        this.setState({
                            signatureStatus: res.data.status
                        }, () => {
                            if (this.state.signatureStatus) {
                                this.getBtcBalance();
                                callSuccessToast("Claim Processing...");
                            } else {
                                callErrorToast(res.data.message);
                            }
                        })
                    })
                    .catch((error) => {
                        console.log(error);
                    })
            }

        } catch (err) {
            return callErrorToast("Something went wrong.")
        }
    }


    // claim bonus funtion 

    claimBonus = async () => {

        let BTCAddress = this.state.btcAddress.toString();
        let btcBalance = this.state.btcBalance.toString();
        console.log(BTCAddress, btcBalance);

        try {
            if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
                const trc20ContractAddress = this.state.contractAddress; //contract address
                console.log("Initiated", trc20ContractAddress)
                let contract = await window.tronWeb.contract().at(trc20ContractAddress);
                let result = await contract.claimBonus(BTCAddress, btcBalance).send();
                this.setState({
                    claimHash: result,
                    generateModalOpen: false,
                    claimHashOpen: true,
                })
            }

        } catch (err) {
            return callErrorToast("Request can not be processed!");
        }
    }


    render() {
        return (
            <React.Fragment>
                {this.state.hostError ?
                    <div class="pageLoader d-flex align-items-center justify-content-center">
                        <div class="d-flex flex-column align-items-center">
                            <div class="sa">
                                <div class="sa-warning">
                                    <div class="sa-warning-body"></div>
                                    <div class="sa-warning-dot"></div>
                                </div>
                            </div>
                            <h5 class="text-center">Please change the network to {HOSTNETWORKNAME}.</h5>
                        </div>
                    </div>
                    : ""}
                <div className="main-content sidebar-open claim-page">
                    {this.state.networkErrorAlert ?
                        <ErrorAlert title="Error Retrieving Data !" />
                        : ""}

                    {window?.tronWeb?.defaultAddress?.base58 ? null : <WarningComponent />}

                    <div className="claim-wrap mb-39">
                        <div className="heading">
                            <h4>Claim</h4>
                        </div>
                        <div className="card">
                            <div className="content text-center mb-39">
                                <h4>Enter your bitcoin address to claim its UTXO. Each address can only be claimed once.</h4>
                            </div>
                            <div className="row">
                                <div className="col-lg-6 mx-auto">
                                    <div className="input-wrap">
                                        <div className="form-group mb-0 d-flex">
                                            <input type="text" onChange={this.handleAddress} value={this.state.btcAddress} className="form-control" placeholder="Bitcoin Address" />
                                            <button type="button" onClick={this.checkbtcAddress} className="px-4 border-0 btn primary-btn btc-check">CHECK</button>
                                        </div>
                                    </div>

                                    {this.state.checkBtcMessage ?
                                        <div className={this.state.checkBtcStatus ? "alert alert-success text-center mt-3" : "alert alert-danger text-center mt-3"}>
                                            {this.state.checkBtcMessage}
                                            {this.state.checkBtcStatus ? <a onClick={this.openGenerateModal} class="text-warning cursor-pointer"> Click Here to claim.</a> : ""}
                                        </div>
                                        : ''}
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="table-wrap mb-39">
                        <div className="heading">
                            <h4>Claim History</h4>
                        </div>
                        <div className="table-responsive empty-table card ">
                            <table className="table">
                                <thead>
                                    <tr>
                                        <th scope="col">Day</th>
                                        <th scope="col">BTC Address</th>
                                        <th scope="col">Raw BTC</th>
                                        <th scope="col">Adjusted BTC</th>
                                        <th scope="col">Claimed RYZ</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {!this.state.dataLoading ?
                                        (this.state.btcRewards && this.state.btcRewards.length > 0) ? this.state.btcRewards.map((i) =>
                                            <tr>
                                                {/* <td>{new Date(parseInt(i.day)).toLocaleString()}</td> */}
                                                <td>{new Date(parseInt(i.Day)).toLocaleString()}</td>
                                                <td>{i.BTCAddress}</td>
                                                <td>{i.BTCAmount}</td>
                                                <td>{i.BTCAmount}</td>
                                                <td>{i.ClaimRYZ}</td>
                                            </tr>
                                        ) : <tr><td className="no-data">No Data Found</td></tr>
                                        : <tr><td className="p-0">
                                            <div className="loader">
                                                <span></span>
                                                <span></span>
                                                <span></span>
                                                <span></span>
                                                <p className="pt-2 text-center">Loading Data from Blockchain... </p>
                                            </div></td></tr>}
                                </tbody>
                            </table>
                            {/* <div className="data-box">
                                <p className="no-data">None</p>
                            </div> */}
                        </div>
                    </div>
                </div>


                {/* generate message modal */}
                <div>
                    <Modal
                        isOpen={this.state.generateModalOpen}
                        onRequestClose={this.closeGenerateModal}
                        style={customStyles}
                    >

                        <div className="modalContainer p-3 position-realtive">
                            <i aria-hidden="true" className="position-absolute modal-close" onClick={this.closeGenerateModal}>&times;</i>
                            <h3 className="text-center">Generate Unique string to verify</h3>
                            <button type="button" className="btn primary-btn w-100 btn-lg mt-4" onClick={this.generateUniqeString}>Generate</button>
                            <div className="input-group my-3">
                                <input type="text" className="form-control p-4" placeholder="Generate unique string" ref={(textarea) => this.textArea = textarea} defaultValue={this.state.uniqueString} />
                                <div className="input-group-append">
                                    <span className="input-group-text d-flex align-items-center justify-content-center primary-btn px-3" id="basic-addon2" onClick={() => this.copyCodeToClipboard()}>
                                        <span><i className="material-icons input-icon align-middle mr-1">content_copy</i> Copy</span></span>
                                </div>
                            </div>
                            {this.state.uniqueString ?
                                <>
                                    <div class="alert alert-success" role="alert">
                                        Copy the above string and generate signature from electron wallet with same btc address.
                                    </div>

                                    <h3 className="mt-4 text-center">Paste unique string here to claim RYZ </h3>
                                    <div className="input-group my-3">
                                        <input type="text" className="form-control p-4" placeholder="Claim RYZ" value={this.state.claimValue} onChange={this.handleClaimValue} />

                                    </div>
                                    <button type="button" className="btn primary-btn w-100 btn-lg mt-1" onClick={this.handleClaimRyz}>Claim RYZ</button>
                                </>
                                : ""}
                        </div>
                    </Modal>
                </div>


                {/* claim hash modal */}
                <div>
                    <Modal
                        isOpen={this.state.claimHashOpen}
                        onRequestClose={this.closeClaimHashModal}
                        style={customStyles}
                    >

                        <div className="modalContainer p-3 position-realtive">
                            <i aria-hidden="true" className="position-absolute modal-close" onClick={this.closeClaimHashModal}>&times;</i>

                            <div className="alert alert-success text-center font-weight-bold mt-4" role="alert">
                                You request is executed on blockchain.
                            </div>
                            <div className="input-group mt-4 mb-3">
                                <input type="text" className="form-control" placeholder="Hash" ref={(textarea) => this.textArea = textarea} value={this.state.claimHash} />
                                <div className="input-group-append">
                                    <span className="input-group-text d-flex align-items-center justify-content-center primary-btn px-3" id="basic-addon2" onClick={() => this.copyCodeToClipboard()}>
                                        <span><i className="material-icons input-icon align-middle mr-1">content_copy</i> Copy Hash</span></span>
                                </div>
                            </div>
                        </div>
                    </Modal>
                </div>

                <ButterToast position={{ vertical: POS_UP, horizontal: POS_RIGHT }} />
            </React.Fragment>
        )
    }
}