import Transfer from '../component/Pages/transfer';
import Stake from '../component/Pages/stake';
import Claim from '../component/Pages/claim'
import Refer from '../component/Pages/refer';
import Transform from '../component/Pages/transform';

const routes = [
	{
		path:'/',
		component: Transfer
	},
	{
		path:'/transfer',
		component: Transfer
	},
	{
		path:'/stake',
		component: Stake

	},
	{
		path:'/claim',
		component: Claim
	},
	{
		path:'/refer',
		component: Refer
	},
	{
		path:'/transform',
		component: Transform
	}
]

export default routes;