import React, { Component } from "react";
import WarningComponent from "../../commoncomponent/warning";
import ErrorAlert from "../../commoncomponent/ErrorAlert";
import axios from "axios";
import { API_ROOT, TOKEN, INTERVAL, HOSTNETWORK, HOSTNETWORKNAME } from '../../config';
import Modal from 'react-modal';
import { callErrorToast, callSuccessToast } from '../../helper';
import ButterToast, { POS_UP, POS_BOTTOM, POS_RIGHT } from 'butter-toast';

const customStyles = {
    content: {
        top: "50%",
        left: "50%",
        right: "auto",
        bottom: "auto",
        marginRight: "-50%",
        transform: "translate(-50%, -50%)"
    }
};

export default class Transform extends Component {
    constructor() {
        super();
        this.state = {
            fromAddress: null,
            allTransform: [],
            getCurrententry: null,
            countdownTimer: null,
            dataLoading: true,
            lastTransform: [],
            yourTotalTrx: [],
            yourTotalRyz: [],
            withdraModalOpen: false,
            enterWithdrawOpen: false,
            contractAddress: '',
            tokenBalance: null,
            enterwithdrawAmount: '',
            withdrawHashOpen: false,
            withdrawHash: '',
            enterWithdrawHash: '',
            enterWithdrawHashOpen: false,
            networkErrorAlert: false,
            trxBalance: '',
            hostError: false,
        }
    }


    getTotalMonthDays(month,year) {
        return new Date(year, month, 0).getUTCDate();
    };

    // countDownTimer() {
    //     var d = new Date();
        
    //     var totalMonthDays = this.getTotalMonthDays(7,d.getUTCFullYear());
    //     var months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
        
    //     var actualMonth = d.getUTCMonth();
    //     if((totalMonthDays-d.getUTCDate()) < 1){
    //         actualMonth = d.getUTCMonth() + 1;
    //     }

    //     var date = d.getUTCDate() + 1;
    //     if(totalMonthDays < date){
    //         date = date - totalMonthDays;
    //     }
    //     var year = d.getUTCFullYear();
                
    //     var advanceDate = months[actualMonth] + ' ' + date + ',' + year + ' ' + '00:00:00';
    //     console.log(advanceDate);
    //     var countDownDate = new Date(advanceDate).getTime();

    //     // Run myfunc every second
    //     var myfunc = setInterval(() => {
    //         var now = new Date();
    //         var nowUTC = new Date(now.getUTCFullYear(), now.getUTCMonth(), now.getUTCDate(), now.getUTCHours(), now.getUTCMinutes(), now.getUTCSeconds());
    //         //var distance = end - nowUTC;
    //        // var now = new Date(currentDate).getTime();
    //         var timeleft = countDownDate - nowUTC;

    //         // Calculating the days, hours, minutes and seconds left
    //         var hours = Math.floor((timeleft % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    //         var minutes = Math.floor((timeleft % (1000 * 60 * 60)) / (1000 * 60));
    //         var seconds = Math.floor((timeleft % (1000 * 60)) / 1000);
    //         this.setState({
    //             countdownTimer: hours + "h:" + minutes + "m:" + seconds + "s"
    //         });
    //         // if (timeleft < 0) {
    //         //     this.setState({
    //         //         countdownTimer: "closed"
    //         //     });
    //         //     clearInterval(myfunc);
    //         // }
    //         //console.log(this.state.countdownTimer)
    //     }, 1000);
    // }


    // withdraw modal 

    countDownTimer(){
        setInterval(() => {
            axios.get(`${API_ROOT}` + '/api/tron/getServerTime', {
                headers: {
                    'Authorization': `Basic ${TOKEN}`,
                },
            })
            .then((serverTime) => {
                this.setState({
                    countdownTimer: serverTime.data.Hours + "h:" + serverTime.data.Min + "m:" + serverTime.data.Sec + "s"
                });
            })

            .catch((error) => {
                console.log(error);
            })
        }, 1000);
    }
    openWithDrawModal = () => {
        this.setState({ withdraModalOpen: true });
    }


    closeWithDrawModal = () => {
        this.setState({ withdraModalOpen: false });
    }


    openEnterWithDrawModal = () => {
        this.setState({ enterWithdrawOpen: true });
    }


    closeEnterWithDrawModal = () => {
        this.setState({ enterWithdrawOpen: false });
    }


    // getting table address
    getData = () => {
        if (window.tronWeb && window.tronWeb.defaultAddress && window.tronWeb.solidityNode.host == HOSTNETWORK) {
            this.setState({
                hostError: false,
            })

            if (window.tronWeb && window.tronWeb.defaultAddress && window.tronWeb.defaultAddress.base58) {
                this.setState({
                    fromAddress: window.tronWeb.defaultAddress.base58
                })

                /** start refferal history */
                axios.get(`${API_ROOT}` + '/api/tron/getAllTransformTableDataByAddress?address=' + this.state.fromAddress, {
                    headers: {
                        'Authorization': `Basic ${TOKEN}`,
                    },
                })
                    .then((resAllTransform) => {

                        let yourRyz = [];
                        let yourTrx = [];

                        resAllTransform.data.map((el) => {
                            yourTrx.push(Number(el.YourTRX));
                            yourRyz.push(Number(el.YourRYZ));
                        })

                        this.setState({
                            networkErrorAlert: false,
                            allTransform: resAllTransform.data,
                            getCurrententry: resAllTransform.data.length - 1,
                            lastTransform: resAllTransform.data[resAllTransform.data.length - 1],
                            dataLoading: false,
                            yourTotalRyz: yourRyz,
                            yourTotalTrx: yourTrx,
                        });
                    })

                    .catch((error) => {
                        this.setState({
                            networkErrorAlert: true,
                        })
                    })


                /** get contract address  */
                axios.get(`${API_ROOT}` + '/api/tron/getContractAddress?address=' + this.state.fromAddress, {
                    headers: {
                        'Authorization': `Basic ${TOKEN}`,
                    },
                })
                    .then((res) => {
                        // console.log("contract address", res.data.address);
                        this.setState({ contractAddress: res.data.address })
                    })
                    .catch((err) => {
                        console.error(err)
                    })
                /** end */


                /** start get token balance */
                axios.get(`${API_ROOT}` + '/api/tron/getTokenBalance?address=' + this.state.fromAddress, {
                    headers: {
                        'Authorization': `Basic ${TOKEN}`,
                    },
                })
                    .then((res) => {

                        this.setState({ tokenBalance: Number(res.data.balance) })
                    })
                    .catch((err) => {
                        console.log(err);
                    })



                /** start get token balance */
                axios.get(`${API_ROOT}` + '/api/tron/getTrxBalance?address=' + this.state.fromAddress, {
                    headers: {
                        'Authorization': `Basic ${TOKEN}`,
                    },
                })
                    .then((res) => {

                        this.setState({ trxBalance: Number(res.data.balance) })
                    })
                    .catch((err) => {
                        console.log(err);
                    })


                /** end */
            } else {
                console.log('still searching ...')
            }
        } else {
            this.setState({
                hostError: true,
            })

        }
    }




    componentDidMount() {
        setInterval(() => {
            this.getData();
        }, `${INTERVAL}`)


        /** Countdown timer */
        this.countDownTimer();
    }


    // withdraw your ryz
    withdrawAmount = async () => {
        try {
            if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
                const trc20ContractAddress = this.state.contractAddress//contract address
                let contract = await window.tronWeb.contract().at(trc20ContractAddress);
                let result = await contract.withdrawPurchasedToken().send({ callValue: this.state.lastTransform.YourRYZ * 1000000 });
                callSuccessToast("You had Successfully withdraw the amount.");
                this.setState({
                    withdraModalOpen: false,
                    withdrawHash: result,
                    withdrawHashOpen: true,
                });

                this.getData();
            }

        } catch (err) {
            return callErrorToast("Request can not be processed!");
        }

    }

    // withdraw hash modal

    closeWithdrawHashModal = () => {
        this.setState({ withdrawHashOpen: false });
    }

    closeEnterWithdrawHashModal = () => {
        this.setState({ enterWithdrawHashOpen: false });
    }

    // settting emter withdraw amount 

    handleEnterAmount = (event) => {
        if (Number(event.target.value) > Number(this.state.trxBalance)) {
            return callErrorToast("Amount should not be more than avaliable balance.");
        } else {
            this.setState({
                enterwithdrawAmount: event.target.value,
            })
        }
    }

    // setting maximam enter amount 
    setMaxAmount = () => {
        this.setState({
            enterwithdrawAmount: this.state.trxBalance,
        })
    }


    // enter amount functionality

    handleEnterWithdrawAmount = async () => {
        if (!this.state.enterwithdrawAmount) {
            return callErrorToast("Please the the amount.");
        }


        if (Number(this.state.enterwithdrawAmount) < 1) {
            return callErrorToast("Enter amount more than 0.");
        }

        try {
            if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
                const trc20ContractAddress = this.state.contractAddress; //contract address
                let contract = await window.tronWeb.contract().at(trc20ContractAddress);
                let result = await contract.purchaseTokens().send({ callValue: this.state.enterwithdrawAmount * 1000000 });

                callSuccessToast("You had Successfully withdraw the amount.");

                this.setState({
                    enterWithdrawOpen: false,
                    enterWithdrawHashOpen: true,
                    enterWithdrawHash: result,
                });

                this.getData();


            }

        } catch (err) {
            return callErrorToast("Request can not be processed!");
        }
    }


    // copy function 
    copyCodeToClipboard = () => {
        const el = this.textArea
        el.select()
        document.execCommand("copy")
        return callSuccessToast("Copied Successfully!");
    }


    render() {
        return (
            <React.Fragment>
                {this.state.hostError ?
                    <div class="pageLoader d-flex align-items-center justify-content-center">
                        <div class="d-flex flex-column align-items-center">
                            <div class="sa">
                                <div class="sa-warning">
                                    <div class="sa-warning-body"></div>
                                    <div class="sa-warning-dot"></div>
                                </div>
                            </div>
                            <h5 class="text-center">Please change the network to {HOSTNETWORKNAME}.</h5>
                        </div>
                    </div>
                    : ""}
                <div className="main-content sidebar-open transform-page">
                    {this.state.networkErrorAlert ?
                        <ErrorAlert title="Error Retrieving Data !" />
                        : ""}

                    {window?.tronWeb?.defaultAddress?.base58 ? null : <WarningComponent />}
                    <div className="table-wrap mb-39 transfer-table">
                        <div className="heading">
                            <h4>Transform</h4>
                        </div>
                        <div className="table-responsive card">
                            <table className="table">
                                <thead>
                                    <tr>
                                        <th scope="col">Day</th>
                                        <th scope="col">RYZ Available</th>
                                        <th scope="col">Total TRX</th>
                                        <th scope="col">RYZ/TRX</th>
                                        <th scope="col">Closing</th>
                                        <th scope="col">Your RYZ</th>
                                        <th scope="col">Your TRX</th>
                                        <th scope="col"></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {!this.state.dataLoading ?
                                        <tr className="lastTransform">
                                            <td>{this.state.lastTransform && this.state.lastTransform.Day}/351</td>
                                            <td>{this.state.lastTransform && this.state.lastTransform.RYZAvailable} RYZ</td>
                                            <td>{this.state.lastTransform && this.state.lastTransform.TotalTRX} TRX</td>
                                            <td>{this.state.lastTransform && this.state.lastTransform.RYZTRX} RYZ</td>
                                            <td>{this.state.countdownTimer}</td>
                                            <td>{this.state.lastTransform && this.state.lastTransform.YourRYZ} RYZ</td>
                                            <td className> {this.state.lastTransform && this.state.lastTransform.YourTRX} TRX</td>
                                            <td>{this.state.lastTransform && this.state.lastTransform.YourRYZ > 0 ?
                                                <button type="button" className="btn-custom-grey" onClick={this.openWithDrawModal}>Withdraw</button> : <button type="button" className="btn-custom-grey" onClick={this.openEnterWithDrawModal}>Enter</button>}</td>
                                        </tr>
                                        : ""}

                                    {!this.state.dataLoading ?
                                        (this.state.allTransform && this.state.allTransform.length > 0) ? this.state.allTransform.slice(0, -1).reverse().map((item, index) => (
                                            <tr key={index}>
                                                <td>{item.Day}/351</td>
                                                <td>{item.RYZAvailable} RYZ</td>
                                                <td>{item.TotalTRX} TRX</td>
                                                <td>{item.RYZTRX} RYZ</td>
                                                <td>closed</td>
                                                <td>{item.YourRYZ} RYZ</td>
                                                <td className>{item.YourTRX} TRX</td>
                                                <td></td>
                                            </tr>
                                        )) : "No Data Found"
                                        : <tr><td className="p-0">
                                            <div className="loader">
                                                <span></span>
                                                <span></span>
                                                <span></span>
                                                <span></span>
                                                <p className="pt-2 text-center">Loading Data from Blockchain... </p>
                                            </div></td></tr>}

                                    <tr className="calculate-wrap">
                                        <td></td>
                                        <td></td>
                                        <td></td>
                                        <td></td>

                                        <td className="calcultate-item">
                                            <span className="ttl-title">Total</span>
                                        </td>
                                        <td className="calcultate-item">
                                            <span className="amount">
                                                {this.state.yourTotalRyz.length > 1 && this.state.yourTotalRyz.reduce((ryz, number) => ryz + number)} RYZ</span>
                                        </td>
                                        <td className="calcultate-item">
                                            <span className="amount">{this.state.yourTotalTrx.length > 1 && this.state.yourTotalTrx.reduce((trx, number) => trx + number)} TRX</span>
                                        </td>
                                        <td></td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>


                {/* withdraw amount modal */}
                <div>
                    <Modal
                        isOpen={this.state.withdraModalOpen}
                        onRequestClose={this.closeWithDrawModal}
                        style={customStyles}
                    >

                        <div className="p-4">
                            <i aria-hidden="true" className="position-absolute modal-close p-2" onClick={this.closeWithDrawModal}>&times;</i>
                            <h2 className="mt-3 text-center">Do you want to withdraw this amount ?</h2>

                            <div className="input-wrap mt-4">
                                <div className="input-group">
                                    <input type="number" className="form-control inputDisabled" value={this.state.lastTransform.YourRYZ} disabled />
                                </div>

                                <button type="button" className="btn primary-btn w-100 btn-lg mt-3" onClick={this.withdrawAmount}>Yes</button>
                                <button type="button" className="btn dark-btn w-100 btn-lg mt-3" onClick={this.closeWithDrawModal}>No</button>

                            </div>
                        </div>
                    </Modal>
                </div>


                {/* enter amount modal */}
                <div>
                    <Modal
                        isOpen={this.state.enterWithdrawOpen}
                        onRequestClose={this.closeEnterWithDrawMoadal}
                        style={customStyles}
                    >

                        <div className="p-4">
                            <i aria-hidden="true" className="position-absolute modal-close p-2" onClick={this.closeEnterWithDrawModal}>&times;</i>
                            {/* <h2 className="mt-3 text-center">Enter amount to withdraw</h2> */}

                            <div className="input-wrap mt-4">
                                <label className="mb-3">Amount in TRX ({this.state.trxBalance} TRX available)</label>
                                {/* <div className="input-group">
                                    <input type="number" className="form-control" value={this.state.enterwithdrawAmount} onChange={this.handleEnterAmount} placeholder="Enter RYZ" aria-describedby="max" max={this.state.tokenBalance} />
                                </div> */}

                                <div className="input-group form-group">
                                    <input type="number" className="form-control" value={this.state.enterwithdrawAmount} onChange={this.handleEnterAmount} placeholder="Enter amount to Transform" aria-describedby="max" max={this.state.tokenBalance} />
                                    <div className="input-group-append">
                                        <span className="input-group-text border-0" id="max">
                                            <span className="prep-main" onClick={this.setMaxAmount}>MAX</span>
                                            <span className="prep-head">TRX </span>
                                        </span>
                                    </div>
                                </div>

                                <button type="button" className="btn primary-btn w-100 btn-lg" onClick={this.handleEnterWithdrawAmount}>Enter</button>
                                <button type="button" className="btn dark-btn w-100 btn-lg mt-3" onClick={this.closeEnterWithDrawModal}>Cancel</button>
                            </div>
                        </div>
                    </Modal>
                </div>


                {/* withdraw hash modal */}
                <div>
                    <Modal
                        isOpen={this.state.withdrawHashOpen}
                        onRequestClose={this.closeWithdrawHashModal}
                        style={customStyles}
                    >

                        <div className="modalContainer p-3 position-realtive">
                            <i aria-hidden="true" className="position-absolute modal-close" onClick={this.closeWithdrawHashModal}>&times;</i>

                            <div className="alert alert-success text-center font-weight-bold mt-4" role="alert">
                                You request is executed on blockchain..
                            </div>
                            <div className="input-group mt-4 mb-3">
                                <input type="text" className="form-control" placeholder="Hash" ref={(textarea) => this.textArea = textarea} value={this.state.withdrawHash} />
                                <div className="input-group-append">
                                    <span className="input-group-text d-flex align-items-center justify-content-center primary-btn px-3" id="basic-addon2" onClick={() => this.copyCodeToClipboard()}>
                                        <span><i className="material-icons input-icon align-middle mr-1">content_copy</i> Copy Hash</span></span>
                                </div>
                            </div>
                        </div>
                    </Modal>
                </div>




                {/* enter withdraw hash modal */}
                <div>
                    <Modal
                        isOpen={this.state.enterWithdrawHashOpen}
                        onRequestClose={this.closeEnterWithdrawHashModal}
                        style={customStyles}
                    >

                        <div className="modalContainer p-3 position-realtive">
                            <i aria-hidden="true" className="position-absolute modal-close" onClick={this.closeEnterWithdrawHashModal}>&times;</i>

                            <div className="alert alert-success text-center font-weight-bold mt-4" role="alert">
                                You request is executed on blockchain.
                            </div>
                            <div className="input-group mt-4 mb-3">
                                <input type="text" className="form-control" placeholder="Hash" ref={(textarea) => this.textArea = textarea} value={this.state.enterWithdrawHash} />
                                <div className="input-group-append">
                                    <span className="input-group-text d-flex align-items-center justify-content-center primary-btn px-3" id="basic-addon2" onClick={() => this.copyCodeToClipboard()}>
                                        <span><i className="material-icons input-icon align-middle mr-1">content_copy</i> Copy Hash</span></span>
                                </div>
                            </div>
                        </div>
                    </Modal>
                </div>

                <ButterToast position={{ vertical: POS_UP, horizontal: POS_RIGHT }} />
            </React.Fragment>
        )
    }
}