import React, { Component } from 'react';
import { Link } from 'react-router-dom';
//import WalletImg from '../../'
export default class Header extends Component {
    constructor() {
        super();
        this.state = {
            fromAddress: null
        }
    }
    showSidebar = (e) => {
        e.preventDefault()
        document.getElementById('mySidebar').style.display = 'block';
    }

    // loginTronlink(){

    // }

    componentDidMount() {
        window.setInterval(() => {
            if (window.tronWeb && window.tronWeb.defaultAddress && window.tronWeb.defaultAddress.base58) {
                this.setState({
                    fromAddress: window.tronWeb.defaultAddress.name
                })
            } else {
                console.log('still searching...')
                this.setState({
                    fromAddress: ''
                })
            }
        }, 2000)
    }

    render() {
        return (
            <React.Fragment>
                <header className="main-header">
                    <nav className="navbar py-0">
                        <div className="left-widgets">
                            <div className="logo">
                                <Link className="logo-img" to="/">
                                    <img className="img-fluid" src="/images/logo.png" alt="logo" width="67" height="78" />
                                </Link>
                            </div>

                            <Link to="#" className="sideNav-icon" onClick={this.showSidebar}>
                                <i className="material-icons menu-icon">
                                    menu
                        </i>
                            </Link>
                        </div>
                        <div className="right-widget d-flex align-items-center">
                        {window?.tronWeb?.defaultAddress?.base58 ? <span className="d-flex align-items-center"><img src="/images/wallet_image.png" className="img-fluid mr-1" width="25"  /><span>{this.state.fromAddress}</span></span> : <button type="button" className="btn secondary-bordered-btn" data-toggle="modal" data-target="#exampleModal">
                                Sign In
                    </button>
                            }

                        </div>
                    </nav>
                </header>
                <div className="modal fade" id="exampleModal" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                    <div className="modal-dialog" role="document">
                        <div className="modal-content p-4 text-center">
                            <div className="modal-header pb-2">
                                <button type="button" className="close p-0" data-dismiss="modal" aria-label="Close">
                                    <i aria-hidden="true">&times;</i>
                                </button>
                            </div>
                            <div className="modal-body pt-2">
                                <h2 className="text-dark font-weight-bold mb-3">Please Login in TronLink</h2>
                                <p className="tron-extens my-4 text-dark">Haven't installed TronLink yet?</p>
                                <a href="https://chrome.google.com/webstore/detail/tronlink/ibnejdfjmmkpcnlpebklmnkoeoihofec" target="_blank"><button type="button" className="btn secondary-btn">Click Here</button></a>
                            </div>
                           
        
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}