import React, {Component} from 'react';
import { Link } from "react-router-dom";

export default class Sidebar extends Component {
    constructor(){
        super();

        this.state = {
            links: [
                {
                    id: 1,
                    name: "Transfer",
                    to: "/",
                    image:'/images/transfer-icon.png',
                    color_image:'/images/transfer-color.png'
                },
                {
                    id: 2,
                    name: "Stake",
                    to: "/stake",
                    image:'/images/stake.png',
                    color_image:'/images/stake-color.png'
                },
                {
                    id: 3,
                    name: "Claim",
                    to: "/claim",
                    image:'/images/claim.png',
                    color_image:'/images/claim-color.png'
                },
                {
                    id: 4,
                    name: "Refer",
                    to: "/refer",
                    image:'/images/reffer.png',
                    color_image:'/images/reffer-color.png'
                },
                {
                    id: 5,
                    name: "Transform",
                    to: "/transform",
                    image:'/images/transform.png',
                    color_image:'/images/transform-color.png'
                }
            ],
            activeLink: null,
            defaultActive:null
        }
    }
    
    handleClick(id){
        this.setState({
            activeLink:id,
            defaultActive:""
        });
    }
    hideSideBar = (e) => {
        e.preventDefault()
        document.getElementById('mySidebar').style.display='none';
    }
    componentDidMount(){ 
        this.setState({
            defaultActive:window.location.pathname
        })
    }
    render(){
        const { links, activeLink} = this.state;
        
        return(
            <React.Fragment>
                <div className="sidebar">
                    <div className="navigation-left"  id="mySidebar">
                        <Link className="sidenav-close" to="#" onClick={this.hideSideBar}>
                            <i className="material-icons font-bold-600">
                            close
                            </i>
                        </Link>
                        <ul className="nav flex-column" >
                            {
                                links.map((link) =>
                                    <li key={link.id} className="nav-item" onClick={() => this.handleClick(link.id)}>
                                        <Link className={"nav-link" +(link.id === activeLink?" active":(link.to == this.state.defaultActive)?" active":"")}  to={link.to}>
                                            <div className="icon-wrap">
                                                <img className="img-fluid white-ico" src={link.image} alt="nav-icon" height="47" width="65" />
                                                <img className="img-fluid color-ico" src={link.color_image} alt="nav-icon" height="47" width="65" />
                                            </div>
                                            <span className="nav-text">{link.name}</span>
                                        </Link>
                                    </li>
                                )
                            }
                        </ul>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}